#back-top {
  width: 6vmin;
  height: 6vmin;
  display: none;
  position: fixed;
  bottom: 30px;
  right: 50px;
  transform: rotate(-45deg);
}

#back-top:before {
  content: "";
  width: 100%;
  height: 100%;
  border-style: solid;
  border-width: .8vmin .8vmin 0 0;
  border-color: var(--janre-color);
  transition: var(--transition-duration, --transition-timing-function);
  transform-origin: 100% 0;
  display: block;
}

#back-top:after {
  content: "";
  float: left;
  width: 104%;
  height: 100%;
  border-style: solid;
  border-width: 0 .8vmin 0 0;
  border-color: var(--janre-color);
  transform-origin: 85% 0;
  transition: var(--transition-duration, --transition-timing-function);
  position: relative;
  top: -100%;
}

#back-top:hover:after {
  border-color: var(--button-hover-color);
  height: 120%;
  transform: rotate(45deg);
}

#back-top:hover:before {
  border-color: var(--button-hover-color);
  transform: scale(.8);
}

#back-top:hover {
  cursor: pointer;
}

